import moveVerify from "@/components/helang-moveVerify/helang-moveVerify.vue"
export default {
	data() {
		return {
			username:"",
			phone:"",
			resultData:{},
			isVerify:false,
		}
	},
	components: {  "move-verify":moveVerify  },
	methods:{
		/* 校验结果回调函数 */
		verifyResult(res){
			console.log(res);
			this.isVerify = true ;
			this.resultData = res;
		},
		/* 校验插件重置 */
		verifyReset(){
			this.$refs.verifyElement.reset();

			/* 删除当前页面的数据 */
			this.resultData = {};
		}
	}
}
